import dayjs from "dayjs";

//    
const turnMinutesToHours = (number) => {
  const MINUTES_IN_HOUR = 60;
  const hours = Math.floor(number / MINUTES_IN_HOUR);
  const minutes = number % MINUTES_IN_HOUR;

  return hours < 1
    ? `${minutes}m`
    : `${hours}h ${minutes}m`;
};

//     ()
const getYDate = (date) => {
  return dayjs(date).format(`YYYY`);
};

//     (  )
const getDMYDate = (date) => {
  return dayjs(date).format(`DD MMMM YYYY`);
};

//     ( /  /   : )
const getYMDHMDate = (date) => {
  return dayjs(date).format(`YYYY/MM/DD HH:mm`);
};

//    
const sortByDate = (movieA, movieB) => dayjs(movieB.releaseDate).diff(movieA.releaseDate);


export {
  turnMinutesToHours,
  getYDate,
  getDMYDate,
  getYMDHMDate,
  sortByDate
};
